.boxs {
  margin: 0.3rem;
  background-color: #1b1622;
  border-radius: 0.25rem;
  width: auto;
  color: #ccc;
  border: 0.05rem solid #a962ff;
}

.boxs .title {
  color: #fcc722;
  font-size: 0.24rem;
  padding-top: 0.2rem;
  text-align: center;
}

.boxs .content {
  font-size: 14px;
  color: #fff;
  text-align: left;
  padding: 0.1rem 0.2rem;
}

.boxs .content .input {
  color: #1098fc;
  cursor: pointer;
  height: 0.4rem;
  line-height: 0.4rem;
  width: 2rem;
  border: none;
  background: #000;
  border-radius: 0.1rem;
}
.boxs .content .button {
  background: #1098fc;
  color: #fff;
  text-align: center;
  height: 0.4rem;
  line-height: 0.4rem;
  width: 1.4rem;
  border-radius: 0.1rem;
  padding: 0 0.2rem;
  border: none;
  margin-left: 0.1rem;
  cursor: pointer;
}
.pad {
  padding: 0.3rem;
}
