@font-face {
  font-family: "iconfont";
  src: url("../public/fonts/iconfont.woff2?t=1704711690435") format("woff2"),
    url("../public/fonts/iconfont.woff?t=1704711690435") format("woff"),
    url("../public/fonts/iconfont.ttf?t=1704711690435") format("truetype");
}

.iconfont {
  font-family: "iconfont" !important;
  font-size: 16px;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-rise:before {
  content: "\e789";
}

.icon-fall:before {
  content: "\e7d7";
}

.icon-withdraw:before {
  content: "\e649";
}

.icon-favorite:before {
  content: "\e629";
}

.icon-share:before {
  content: "\e60f";
}

.icon-facebook:before {
  content: "\e601";
}

.icon-disconnect:before {
  content: "\e67f";
}

.icon-twitter:before {
  content: "\e652";
}

::-webkit-scrollbar {
  width: 0.1rem;
}

::-webkit-scrollbar-track {
  background-color: #000;
}

::-webkit-scrollbar-thumb {
  background-color: #a962ff;
  border-radius: 5px;
}

::-webkit-scrollbar-thumb:hover {
  background-color: #555;
}

body {
  margin: 0;
  padding: 0;
  overflow: hidden;
  font-family: DSemi, monospace, "Arial", sans-serif;
  background: #000508;
}

.app {
  max-width: 7.5rem;
  display: block;
  margin: 0 auto;
  background-image: radial-gradient(
      farthest-side at bottom left,
      #271d3b,
      transparent 900px
    ),
    radial-gradient(
      farthest-corner at bottom right,
      #3a233f,
      transparent 1000px
    );
  overflow: overlay;
  height: 100vh;
}

.topbar-header {
  background: linear-gradient(
    101.81deg,
    rgb(240, 135, 255) -4.52%,
    rgb(110, 31, 206) 54.5%,
    rgba(110, 31, 206, 0.2) 101.41%
  );
  color: #fff;
  height: 0.8rem;
  line-height: 0.8rem;
  display: flex;
}

.topbar-header .tools {
  width: 70%;
  text-align: left;
  display: flex;
  justify-content: left;
  align-items: center;
}

.topbar-header .tools .back-button {
  background-color: transparent;
  border: none;
  color: #fff;
  font-size: 0.32rem;
  cursor: pointer;
}

.topbar-header .tools .iconfont {
  padding: 0.1rem;
  font-size: 0.42rem;
  cursor: pointer;
  position: relative;
}

.topbar-header .tools .tooltip {
  text-align: center;
  width: 3.2rem;
  height: 0.2rem;
  line-height: 0.2rem;
  font-size: 12px;
  position: absolute;
  background-color: #333;
  color: #fff;
  padding: 0.05rem;
  border-radius: 0.05rem;
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.3s ease, visibility 0.3s ease;
}

.topbar-header .tools .iconfont:hover .tooltip {
  visibility: visible;
  opacity: 1;
}

.topbar-header .operations {
  width: 30%;
  text-align: center;
}

.topbar-header .operations .wallet-button {
  background: rgba(0, 0, 0, 0.5);
  border: none;
  color: #fff;
  font-size: 16px 0.32rem;
  cursor: pointer;
  border-radius: 0.25rem;
  padding: 0.1rem 0.2rem;
  text-align: right;
}

.topbar-header .operations .wallet-button .iconfont {
  padding: 0.1rem;
}

.topbar-header h2 {
  font-size: 0.32rem;
}

.right-menus {
  position: fixed;
  top: 7%;
  width: 1.2rem;
  margin-left: 7.3rem;
  height: auto;
  text-align: center;
}

.right-menus .li {
  height: 0.4rem;
  line-height: 0.4rem;
  background: #a962ff;
  border-radius: 0 0.1rem 0.1rem 0;
  margin-bottom: 0.1rem;
  cursor: pointer;
  font-size: 14px;
}

.right-menus .li .select {
  border: 0.01rem solid #a962ff;
  background: #a962ff;
  color: #fff;
  border-radius: 0.1rem;
  cursor: pointer;
  text-align: center;
  font-size: 14px;
}

.modal-layer {
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  position: fixed;
  z-index: 2;
  color: hsl(220, 50%, 90%);
  background: hsl(220, 25%, 10%);
  display: none;
}

.modal {
  color: hsl(220, 50%, 90%);
  background: linear-gradient(to right, hsl(210, 30%, 20%), hsl(255, 30%, 25%));
  border-radius: 0.1rem;
  box-shadow: var(--m-shadow, 0.4rem 0.4rem 10.2rem 0.2rem)
    hsla(236, 50%, 50%, 0.3);
  padding: 0.1rem;
  width: 7.5rem;
  text-align: center;
  position: fixed;
  overflow: hidden;
  margin: 0 auto;
  top: 25%;
  left: 0;
  right: 0;
  z-index: 3;
  display: none;
}

.modal .modal-text {
  padding: 0.2rem;
  text-align: center;
  color: #fff;
  font-size: 0.28rem;
  line-height: 0.5rem;
  animation: bounceAnimation 1s infinite;
  background: linear-gradient(
    97.65deg,
    #19fb9b 11.36%,
    #199890 54.3%,
    #005f59 100.78%
  );
  background-clip: border-box;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

@keyframes bounceAnimation {
  0%,
  20%,
  50%,
  80%,
  100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-20px);
  }
  60% {
    transform: translateY(-10px);
  }
}

.modal .button-group {
  display: flex;
  justify-content: space-between;
  padding: 0.2rem;
}

.modal .button-group .button {
  flex: 1;
  padding: 0.2rem;
  border: none;
  border-radius: 0.15rem;
  cursor: pointer;
}

.modal .button-group .cancel-button {
  background-color: #15202b;
  color: #fff;
  margin-right: 0.1rem;
}

.modal .button-group .confirm-button {
  background-color: #1d9bf0;
  color: #fff;
  margin-left: 0.1rem;
}

.metamask-button {
  width: 40%;
  background: linear-gradient(
    90deg,
    #14f195,
    #80ecff 40.1%,
    #64a8f2 82.29%,
    #64a8f2
  );
  color: #fff;
  padding: 0.1rem 0.2rem;
  border: none;
  border-radius: 0.1rem;
  cursor: pointer;
  display: flex;
  align-items: center;
  margin: 0 auto;
  text-align: center;
  margin-bottom: 0.3rem;
}

.metamask-button .metamask-icon {
  width: 0.3rem;
  height: 0.3rem;
  margin-right: 0.2rem;
}

.text-coin {
  color: #1d9bf0;
  font-weight: bolder;
  padding: 0 0.1rem;
}

.text-address {
  color: #1d9bf0;
  font-weight: bolder;
  padding: 0 0.1rem;
  text-decoration: underline;
}
