.head {
  color: #fff;
  text-align: center;
  padding: 0.2rem;
}

.head p {
  padding: 0.1rem;
  margin: 0.1rem;
}

.head .h {
  font-size: 0.46rem;
  font-weight: 600;
  padding: 0.3rem;
}

.head .sub {
  font-size: 0.24rem;
}

.head .tips {
  font-size: 0.28rem;
  padding: 0.2rem;
}

.head .tips .eth {
  color: #fcc722;
  font-size: 0.36rem;
  padding: 0 0.05rem;
}

.head .message {
  color: #c4c4c4;
  font-size: 0.24rem;
  letter-spacing: -0.8px;
  width: 80%;
  text-align: center;
  margin: 0 auto;
}

.head .help {
  margin: 0.4rem 0.1rem 0 0.1rem;
  background-color: #1b1622;
  border-radius: 0.25rem;
  width: auto;
  color: #ccc;
  border: 0.05rem solid #a962ff;
}

.head .help .h2 {
  color: #fcc722;
  font-size: 0.36rem;
  padding-top: 0.2rem;
}

.head .help .content {
  font-size: 14px 0.28rem;
  color: #fff;
  text-align: left;
  padding: 0.1rem 0.2rem;
}

section {
  padding: 20px;
  text-align: center;
}

.prediction-container {
  margin: 0.3rem;
  background-color: #1b1622;
  border-radius: 0.25rem;
  width: auto;
  color: #ccc;
  border: 0.05rem solid #a962ff;
}

.prediction-container .progress-container {
  width: 94%;
  background: #ff3b6b;
  border-radius: 5px;
  overflow: hidden;
  margin: 0 auto;
}

.prediction-container .progress-title {
  text-align: center;
  line-height: 30px;
  color: #ccc;
  font-weight: bold;
}

.prediction-container .progress-title .index {
  color: rgb(31, 207, 241);
  padding: 0 0.1rem;
}

.prediction-container .progress-container .progress-bar {
  height: 0.2rem;
  width: 0;
  transition: width 0.3s ease;
  background: #19fb9b;
  color: #fff;
}

.prediction-container .prediction-options {
  display: flex;
  justify-content: space-around;
}

.prediction-container .prediction-options .option-button {
  width: 50%;
  padding: 0.1rem 0.2rem;
  color: #fff;
  text-decoration: none;
  font-weight: bold;
  transition: background-color 0.3s;
  background-clip: border-box;
  font-size: 0.28rem;
  border-radius: 0.05rem;
  margin: 0.2rem;
}

.prediction-container .prediction-options .option-button:hover {
  font-size: 0.28rem;
  cursor: pointer;
  background: linear-gradient(
    to bottom,
    #1fcff1 15.61%,
    #234cb6 41.38%,
    rgba(35, 76, 182, 0.1) 79.94%
  );
}

.prediction-container .prediction-options .option-button .iconfont {
  padding: 0rem;
  font-size: 0.28rem;
}

.prediction-container .prediction-options .prediction-warn {
  color: #fcc722;
  font-size: 16px 0.32rem;
  padding-top: 0.2rem;
}

.prediction-container .prediction-options .rise {
  background-image: linear-gradient(to left, #19fb9b 0%, #005f59 100%);
}

.prediction-container .prediction-options .fall {
  background-image: linear-gradient(to left, #ff0844 0%, #ffb199 100%);
}

.prediction-container .prediction-message {
  padding: 0.1rem;
}

.prediction-container .prediction-message .title {
  font-size: 0.24rem;
  padding: 0 0 0.2rem 0;
}

.prediction-container .prediction-message .result {
  line-height: 0.4rem;
  height: 0.4rem;
}

.prediction-container .prediction-message .result .result-rise {
  background-image: linear-gradient(to left, #19fb9b 0%, #005f59 100%);
  color: #fff;
  font-size: 0.28rem;
}

.prediction-container .prediction-message .result .result-fall {
  background-image: linear-gradient(to left, #ff0844 0%, #ffb199 100%);
  color: #fff;
  font-size: 0.28rem;
}

.prediction-container .prediction-message .subtitle {
  padding: 0 0 0.1rem 0;
  color: #fcc722;
  font-size: 16px 0.24rem;
}

.prediction-container .prediction-message .subtitle a {
  color: #c4c4c4;
  font-size: 18px 0.24rem;
  letter-spacing: -0.8px;
  text-decoration: underline;
  line-height: 0.4rem;
}
.participation-list {
  margin: 0.3rem;
  border-radius: 0.25rem;
  background: #1b1622;
  color: #ccc;
}

.participation-list .list-title {
  padding-bottom: 0.2rem;
  font-size: 18px 0.32rem;
}

.participation-list .list-item {
  padding: 0.1rem;
  display: flex;
  flex-direction: column;
  background: #111;
  border-radius: 0.1rem;
  margin: 0.1rem 0;
}

.participation-list .list-item .item-row {
  display: flex;
  padding: 0.05rem 0;
}

.participation-list .list-item .item-row .time {
  font-size: 14px 0.24rem;
}

.participation-list .list-item .item-row .result {
  font-size: 16px 0.28rem;
  color: #fff;
  width: 60%;
  text-align: left;
}

.participation-list .list-item .item-row .yes {
  background: linear-gradient(
    97.65deg,
    #19fb9b 11.36%,
    #199890 54.3%,
    #005f59 100.78%
  );
  background-clip: border-box;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.participation-list .list-item .item-row .no {
  background: linear-gradient(
    97.65deg,
    #ff0844 11.36%,
    #ffb199 54.3%,
    #005f59 100.78%
  );
  background-clip: border-box;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.participation-list .list-item .item-row .wait {
  background: linear-gradient(
    97.65deg,
    #fcc722 11.36%,
    #ffb199 54.3%,
    #c3d104 100.78%
  );
  background-clip: border-box;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  padding: 0 0.05rem;
}

.participation-list .list-item .item-row .yellow {
  color: #fcc722;
  padding-left: 0.08rem;
  text-align: left;
}

.participation-list .list-item .item-row .blue {
  color: #1fcff1;
  padding-left: 0.08rem;
  text-align: left;
}

.participation-list .list-item .item-row .coin-yes {
  color: #19fb9b;
  width: 20%;
  text-align: left;
}

.participation-list .list-item .item-row .coin-no {
  color: #ff3b6b;
  width: 20%;
  text-align: left;
}

.participation-list .list-item .item-row .coin-wait {
  color: #fcc722;
  width: 20%;
  text-align: left;
}

.participation-list .list-item:last-child {
  border-bottom: none;
}

.participation-list .list-item .item-row .withdrawn {
  color: #2ecc71;
  width: 80%;
  text-align: right;
}

.participation-list .list-item .item-row .withdrawn .withdraw-button {
  color: #fff;
  border: none;
  border-radius: 0.05rem;
  cursor: pointer;
  background: linear-gradient(
    101.81deg,
    rgb(240, 135, 255) -4.52%,
    rgb(110, 31, 206) 54.5%,
    rgba(110, 31, 206, 0.2) 101.41%
  );
}

.participation-list .list-not {
  color: #ccc;
  font-size: 18px 0.32rem;
  background: #111;
  border-radius: 0.1rem;
  margin: 0.1rem rem 0;
  text-align: center;
  padding: 0.3rem;
}

.participation-list .list-not .iconfont {
  font-size: 0.32rem;
}
