.boxs {
  margin: 0.3rem;
  background-color: #1b1622;
  border-radius: 0.25rem;
  width: auto;
  color: #ccc;
  border: 0.05rem solid #a962ff;
}

.boxs .title {
  color: #fcc722;
  font-size: 0.24rem;
  padding-top: 0.2rem;
  text-align: center;
}

.boxs .content {
  font-size: 14px;
  color: #fff;
  text-align: left;
  padding: 0.1rem 0.2rem;
}
.boxs .content .link {
  color: #1098fc;
  cursor: pointer;
}
